.RiskParent {
  clear: both;
  border-radius: 5px;
  -webkit-animation: swing-in-top-fwd 0.5s
    cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  animation: swing-in-top-fwd 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #999;
  margin-bottom: 20px;
}
.RiskLeft {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.RiskLeft h3 {
  width: 60%;
  padding: 10px 15px;
  background-color: #999;
  margin: 0;
  color: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 500;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  margin-top: 10px;
}
.RiskLeft p {
  padding: 10px 15px;
  margin: 0;
}
.RiskRight {
  width: 150px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-item-align: center;
      align-self: center;
}
.HighRisk {
  border: 1px solid #e73030;
}
.HighRisk h3 {
  background-color: #e73030;
}

.IndicatorParent {
  width: 93px;
  margin: auto;
  position: relative;
  text-align: center;
}
.IndicatorContainer {
  width: 93px;
  height: 50px;
  background-image: url(../../../../Asserts/Images/indicator_circle.png);
  background-repeat: no-repeat;
  position: relative;
}
.NibIndicator {
  width: 10px;
  height: 10px;
  border: 3px solid #666;
  border-radius: 50%;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  z-index: 2;
  background-color: #fff;
}
.LineIndicator {
  width: 4px;
  height: 24px;
  background-color: #666;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 8px;
  margin: auto;
  border-radius: 20px;
  z-index: 1;
  -webkit-transform-origin: bottom center;
      -ms-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-transform: rotate(55deg);
      -ms-transform: rotate(55deg);
          transform: rotate(55deg);
  -webkit-animation: hot 1s;
          animation: hot 1s;
}
@-webkit-keyframes hot {
  0% {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  100% {
    -webkit-transform: rotate(55deg);
            transform: rotate(55deg);
  }
}
@keyframes hot {
  0% {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
  100% {
    -webkit-transform: rotate(55deg);
            transform: rotate(55deg);
  }
}



